#logo {
    width: 120px;
    background-color: #efefef;
  }
  #head-container,
  #head-left {
    display: flex;
  }
  header{
    /* margin-top: -50px;   */
    width: 100%;
    box-shadow: 2px 4px 2px rgb(17 16 16 / 50%);
    /* position: fixed; */
    height: 70px;
  
    background-color: #dddddd;
  }
  #head-con2 {
    margin-top: -100px;
    width: 100%;
    box-shadow: 2px 4px 2px rgb(17 16 16 / 50%);
    position: fixed;
    height: 70px;
  
    background-color: #dddddd;
  }
  
  #head-con3 {
    margin-top: -70px;
    width: 100%;
    box-shadow: 2px 4px 2px rgb(17 16 16 / 50%);
    position: fixed;
    height: 70px;
  
    background-color: #dddddd;
  }
  #head-right {
    display: flex;
    width: 55%;
  }
  #head-left {
    width: 45%;
  }
  #home-button {
    margin-top: 10px;
    width: 10%;
    margin-left: 2%;
    height: 50px;
    width: 100px;
    background-color: #001f3f;
    border: 0px;
    font-size: 15px;
    font-weight: 600;
    border-radius: 5px;
    color: #ffffff;
    cursor: pointer;
    margin-left: 10px;
  }
  #home-button:hover {
    box-shadow: 1px 1px 5px #5243c0, -0.1px -0.1px 10px rgb(165, 161, 161);
    cursor: pointer;
  }
  .add-button {
    cursor: pointer;
    background-color: #5243c0;
    border: 0px;
    font-size: 15px;
    font-weight: 600;
    border-radius: 5px;
    color: #ffffff;
    padding: 5px 20px;
    margin: 10px;
  }
  .add-button:hover {
    box-shadow: 0px 0px 5px #5243c0, -0.1px -0.1px 10px rgb(165, 161, 161);
    cursor: pointer;
  }
  
  .search-input {
    margin-top: 10px;
    margin-right: 10px;
    background: #fff;
    width: 55%;
    border-radius: 5px;
    position: relative;
    height: 48px;
    box-shadow: 0px 1px 5px 3px rgba(0, 0, 0, 0.12);
  }
  .search-input input {
    height: 48px;
    width: 100%;
    outline: none;
    border: none;
    border-radius: 5px;
    padding: 0 60px 0 20px;
    font-size: 18px;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.1);
  }
  .search-input.active input {
    border-radius: 5px 5px 0 0;
  }
  .search-input .autocom-box {
    padding: 0;
    opacity: 0;
    pointer-events: none;
    max-height: 280px;
  }
  .search-input.active .autocom-box {
    padding: 10px 8px;
    opacity: 1;
    pointer-events: auto;
  }
  .autocom-box li {
    list-style: none;
    padding: 8px 12px;
    display: none;
    width: 100%;
    cursor: default;
    border-radius: 3px;
  }
  .search-input.active .autocom-box li {
    display: block;
    background-color: white;
    box-shadow: 1px 1px 2px gray;
  }
  .autocom-box li:hover {
    background: #efefef;
  }
  .search-input .icon {
    position: absolute;
    right: 0px;
    top: 0px;
    height: 55px;
    width: 55px;
    text-align: center;
    line-height: 55px;
    font-size: 20px;
    color: #644bff;
    cursor: pointer;
  }
  ul,li{
    list-style-type:none
  }